.container{
  width: 540px;
  height: auto;
  .header{
    border-bottom: 1px solid #F3F6F8;
    .header_content{
      padding: 32px;
      display: flex;
      align-items: center;
      .header_content_title{
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        letter-spacing: 0.4px;
        color: #0B132A;
      }
      .close {
        cursor: pointer;
        width: 22px;
        height: 22px;
        opacity: 0.3;
      }
      .close:hover {
        opacity: 1;
      }
      .close:before, .close:after {
        position: absolute;
        top: 38px;
        right: 50px;
        content: ' ';
        height: 23px;
        width: 2px;
        background-color: #333;
      }
      .close:before {
        transform: rotate(45deg);
      }
      .close:after {
        transform: rotate(-45deg);
      }
    }
  }
  .content{
    padding: 10px 32px;
    height: 650px;
    overflow-y: auto;
  }
}
.textField{
  width: 100%;
  margin-top: 26px !important;
  border: none;
  border-radius: 15px;
  label{
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    display: flex;
    align-items: center;
    color: #8F92A1;
  }
}
.subTextField{
  width: 100%;
  margin-top: 10px !important;
  border: none;
  border-radius: 15px;
  label{
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    display: flex;
    align-items: center;
    color: #8F92A1;
  }
}
.title{
  margin: 40px 0 0 0;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #0B132A;
  mix-blend-mode: normal;
}
.sub_data{
  margin-left: 80px;
}
.footer{
  margin-top: 32px;
  margin-bottom: 32px;
  display: flex;
  justify-content: space-between;
  .add_btn{
    background: #FF494D;
    border-radius: 16px;
    padding: 12px 78px;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
    cursor: pointer;
    &:hover{
      opacity: .6;
    }
  }
  .cancel_btn{
    background: rgba(143, 146, 161, 0.1);
    border-radius: 16px;
    padding: 12px 78px;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #000000;
    cursor: pointer;
    &:hover{
      opacity: .6;
    }
  }
}